import React from "react";
import { NavHashLink as Link } from "react-router-hash-link";
import { Container, Row, Col } from "react-bootstrap";
import Map from "./../components/map";
import { withState } from "./../Context";
import "./footer.scss";

const Footer = withState(
  ({
    state: {
      name,
      scrollWithOffset,
      translations: { t }
    },
    location: { pathname }
  }) => (
    <footer style={{ flexShrink: 0 }}>
      {pathname === "/contact-us" && (
        <div style={{ height: "400px", width: "100%" }}>
          <Map />
        </div>
      )}
      <section className="footer-info">
        <Container>
          <Row>
            <Col
              sm={12}
              md={9}
              className="align-self-center text-center text-md-left"
            >
              <div className="widget about-widget">
                <div className="widget-content">
                  {name && <h3>{name}</h3>}
                  <p className="m-0">Enjoy your holiday the best you can!</p>
                </div>
              </div>
            </Col>
            <Col
              sm={12}
              md={3}
              className="text-center text-md-right mt-4 mt-md-0"
            >
              <div className="widget links-widget">
                <h3 className="widget-title">
                  <span>Quick Links</span>
                </h3>
                <div className="widget-content">
                  <Row>
                    <Col>
                      <ul className="custom-list m-0">
                        <li>
                          <Link
                            smooth
                            to="/#"
                            scroll={(el) => scrollWithOffset(el)}
                          >
                            {t("navbarHome")}
                          </Link>
                        </li>
                        <li>
                          <Link
                            smooth
                            to="/rab-island#"
                            scroll={(el) => scrollWithOffset(el)}
                          >
                            {t("navbarIslandRab")}
                          </Link>
                        </li>
                        <li>
                          <Link
                            smooth
                            to="/#apartments"
                            scroll={(el) => scrollWithOffset(el)}
                          >
                            {t("navbarApartments")}
                          </Link>
                        </li>
                      </ul>
                    </Col>
                  </Row>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </section>
      <section className="copyright">
        <Container>
          <Row>
            <Col>
              <p
                className="m-0 text-center text-md-left"
                dangerouslySetInnerHTML={{
                  __html: t("footerCopyright", {
                    year: new Date().getFullYear()
                  })
                }}
              ></p>
            </Col>
          </Row>
        </Container>
      </section>
    </footer>
  )
);

export default Footer;
