import React from "react";
import GoogleMapReact from "google-map-react";
import { mapStyles } from "./../components/mapStyles";

const Map = () => {
  const loc = {
    center: {
      lat: 44.828537,
      lng: 14.748109
    },
    marker: {
      lat: 44.828537,
      lng: 14.748109
    },
    zoom: 11
  };

  const AnyReactComponent = ({ text }) => (
    <div
      style={{
        color: "white",
        background: "var(--primary)",
        padding: "10px",
        height: "50px",
        width: "50px",
        display: "inline-flex",
        textAlign: "center",
        alignItems: "center",
        justifyContent: "center",
        borderRadius: "100%",
        transform: "translate(-25px, -25px)"
      }}
    >
      {text}
    </div>
  );

  return (
    <GoogleMapReact
      bootstrapURLKeys={{ key: process.env.REACT_APP_MAP_KEY }}
      defaultCenter={loc.center}
      defaultZoom={loc.zoom}
      options={{
        scrollwheel: false,
        mapTypeControlOptions: {
          position: "BOTTOM_RIGHT"
        },
        draggable: false,
        rotateControl: false,
        scaleControl: false,
        streetViewControl: false,
        panControl: false,
        zoomControlOptions: false,
        styles: mapStyles()
      }}
    >
      <AnyReactComponent
        lat={loc.marker.lat}
        lng={loc.marker.lng}
        text="Mario Rab"
      ></AnyReactComponent>
    </GoogleMapReact>
  );
};

export default Map;
