import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import translationEN from "./translations/mario-rab_English.json";
import translationHR from "./translations/mario-rab_Croatian.json";
import translationIT from "./translations/mario-rab_Italian.json";
import translationDE from "./translations/mario-rab_German.json";

// the translations
const resources = {
  en: {
    translation: translationEN
  },
  hr: {
    translation: translationHR
  },
  it: {
    translation: translationIT
  },
  de: {
    translation: translationDE
  }
};

i18n
  .use(initReactI18next) // passes i18n down to react-i18next
  .init({
    resources,
    lng: "en",

    keySeparator: false, // we do not use keys in form messages.welcome

    interpolation: {
      escapeValue: false // react already safes from xss
    }
  });

export default i18n;
