import React, { Suspense, lazy } from "react";
import { Switch, Route, BrowserRouter } from "react-router-dom";
import Loader from "./components/loader";
import Footer from "./components/footer"
import "./styles.scss";
import { useTranslation } from "react-i18next";

const App = () => {
  useTranslation();

  const HomePage = lazy(() => import("./pages/home.page"));
  const IslandRabPage = lazy(() => import("./pages/island-rab.page"));
  const AboutUsPage = lazy(() => import("./pages/about-us.page"));
  const ContactUsPage = lazy(() => import("./pages/contact-us.page"));
  const HouseRulesPage = lazy(() => import("./pages/house-rules.page"));
  const TermsPage = lazy(() => import("./pages/terms.page"));
  const ApartmentPage = lazy(() => import("./pages/apartment.page"));
  const MainNavigation = lazy(() => import("./components/main-navigation"));

  return (
    <BrowserRouter>
      <Suspense fallback={<Loader />}>
        <div style={{ flex: "1 0 auto" }}>
          <MainNavigation />
          <Switch>
            <Route exact path="/" component={HomePage} />
            <Route exact path="/rab-island" component={IslandRabPage} />
            <Route
              exact
              path="/apartment/:apartmentNumber"
              render={(props) => <ApartmentPage {...props} />}
            />
            <Route exact path="/about-us" component={AboutUsPage} />
            <Route exact path="/contact-us" component={ContactUsPage} />
            <Route exact path="/house-rules" component={HouseRulesPage} />
            <Route exact path="/terms-and-conditions" component={TermsPage} />
          </Switch>
        </div>
        <Route render={(props) => <Footer {...props} />} />
      </Suspense>
    </BrowserRouter>
  );
};

export default App;
